import React, { useState } from 'react';
import { Button, Input, Flex } from '@aws-amplify/ui-react';

const FilterParagraphs = ({ filterState, setFilter }) => {
  const [filterContent, setFilterContent] = useState(''); // New state for handling the input box value
  
  // Define your custom filters here
  const customFilters = [
    "Gk Says",
    // "Source Filter",
    "",
  ];

  const handleKeyPress = (event) => {
    if (event.key === 'Enter') {
      setFilter(event.target.value);
    }
  };

  const handleFilterButtonClick = (filterValue) => {
    setFilter(filterValue); // Assuming setFilter sets the filter in the parent component
    setFilterContent(filterValue); // Also set the input box to display this filter value
  };

  return (
    <div style={{ flex: 1 }}>
      <Input
        type="text"
        placeholder="Filter sites with text, source, or tags"
        value={filterContent}
        onChange={(e) => setFilterContent(e.target.value)}
        onKeyPress={handleKeyPress}
      />
      <div style={{ marginTop: '1rem' }}>
        <Flex direction="row" gap="0.5rem">
          {customFilters.map((filter, index) => (
            <Button key={index} onClick={() => handleFilterButtonClick(filter)} size="small">
              {filter == '' ? 'clear' : filter}
            </Button>
          ))}
        </Flex>
      </div>
    </div>
  );
};

export default FilterParagraphs;