// src/components/AddParagraphModal.js
import React, { useState } from 'react';
import Modal from 'react-modal';
import { Button, TextField, Flex } from '@aws-amplify/ui-react';

// Make sure to bind modal to your appElement (http://reactcommunity.org/react-modal/accessibility/)
Modal.setAppElement('#root');

const AddParagraphModal = ({ onClose, onAdd, editingParagraph, category  }) => {
  const [text, setText] = useState(editingParagraph ? editingParagraph.Text : '');
  const [sourceLink, setSourceLink] = useState(editingParagraph ? editingParagraph.SourceLink : '');
  const [tags, setTags] = useState(editingParagraph ? editingParagraph.Tags : '');

  const addOrEditParagraph  = async () => {
    const isEditing = !!editingParagraph;
    const url = `https://l1az6ozvxa.execute-api.us-east-1.amazonaws.com/alpha/gkcite/api`;
    const method = isEditing ? 'PUT' : 'POST';

    category = (editingParagraph && editingParagraph.Category) || category;
    const body = JSON.stringify(isEditing ? { paragraph_id:editingParagraph.ParagraphId, text, source_link: sourceLink, tags, category } : { text, source_link: sourceLink, tags, category});
  
    try {
      const response = await await fetch(url, {
        method: method,
        headers: {
          "Content-Type": "application/json",
          "Authorization": `${btoa("123456")}:${btoa(localStorage.getItem("password"))}`
        },
        body: body,
    });

    if (!response.ok) {
      throw new Error(`Error adding paragraph. Status: ${response.status}`);
    }

    // Assuming the response contains the newly added paragraph
    const newParagraph = await response.json();
    onAdd();
    // Use the new paragraph data as needed
    console.log('New paragraph added:', newParagraph);
  } catch (error) {
    console.error('Error adding paragraph:', error);
  }
};

  const customStyles = {
    content: {
      padding: '20px',
      borderRadius: '4px',
      overflow: 'visible',
      maxWidth: '1800px', // Max width for desktops and larger screens
      width: '90%', // Default width for smaller screens
      position: 'absolute',
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
      // Other styles you might want to include...
    },
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.75)' // Dimmed background for overlay
    }
  };

  return (
    <Modal
      isOpen
      onRequestClose={onClose}
      style={customStyles}
      class="ReactModal__Content"
    >
      <Button
        onClick={onClose}
        style={{
          position: 'absolute',
          top: '10px',
          right: '10px',
          padding: '0',
          lineHeight: '1'
        }}
        ariaLabel="Close modal"
      >
        &times; {/* Represents a 'close' symbol */}
      </Button>

      <h2>Add Paragraph</h2>

      <Flex direction="column" gap="1rem">
        <TextField
          label="Source Link"
          placeholder="Enter source link"
          value={sourceLink}
          onChange={(e) => setSourceLink(e.target.value)}
        />
        <TextField
          as="textarea"
          label="Text"
          placeholder="Enter paragraph text"
          value={text}
          onChange={(e) => setText(e.target.value)}
          rows={4}
          style={{
            height:'50vh'
          }}
        />
        <TextField
          label="Tags"
          placeholder="Enter tags (comma-separated)"
          value={tags}
          onChange={(e) => setTags(e.target.value)}
        />
        <Button 
          onClick={addOrEditParagraph }
          variation="primary"
          isFullWidth={true}
        >
          Add Paragraph
        </Button>
      </Flex>
    </Modal>
  );
};

export default AddParagraphModal;