// src/components/ParagraphList.js
import React, {useEffect, useState} from 'react';
import { Image,Icon, Card, Button, Grid, View } from '@aws-amplify/ui-react';
import '@aws-amplify/ui-react/styles.css';
import Markdown from 'react-markdown'
import Masonry from 'masonry-layout';
import imagesLoaded from 'imagesloaded';
import '../App.css'

const ReadMoreParagraph = ({ text }) => {
  const [showAll, setShowAll] = useState(false);
  
  const toggleShowAll = () => {
    setShowAll(!showAll);
  };

  return (
    <p>
      {showAll ? text : `${text.slice(0, 500)}...`}
      {!showAll && (
        <button onClick={toggleShowAll}>Read More</button>
      )}
    </p>
  );
};

const ParagraphList = ({ paragraphs, onDelete, onEdit, isReadOnly }) => {
  const [style, setStyle] = useState({display: 'none'});

  const initializeMasonry = () => {
    let $grid = new Masonry('.grid', {
      itemSelector: '.grid__item',
      columnWidth: '.grid__col-sizer',
      gutter: '.grid__gutter-sizer',
      percentPosition: true,
      stagger: 30,
      visibleStyle: { transform: 'translateY(0)', opacity: 1 },
      hiddenStyle: { transform: 'translateY(100px)', opacity: 0 },
    });

    imagesLoaded('.grid').on('progress', function () {
      // console.log("CALLED")
      $grid.layout();
    });
  };


  // Use useEffect to call initializeMasonry after component mount
  useEffect(() => {
    // setTimeout(() => {
      // do something here 1 sec after current has changed
        initializeMasonry();
    // }, 10000);
  }, [paragraphs]); // No dependencies, run once after mount

  // Sort paragraphs by creation date in descending order
  const sortedParagraphs = paragraphs.sort((a, b) => new Date(b.UpdatedAt) - new Date(a.UpdatedAt));

     return (
      <div className="grid" >
        <div className="grid__col-sizer"></div>
        <div className="grid__gutter-sizer"></div>

        {sortedParagraphs.map(paragraph => (

        <div
        className="grid__item"
        // style={{border:'1px solid black'}}
        key={paragraph.ParagraphId}
        >
         
        <Card
          key={paragraph.ParagraphId}
          position="relative"
          padding="1rem"
          boxShadow="small"
          maxWidth={{ base: '100%', xxl: '100%' }} // Responsive max-width based on device
          border="1px solid #ddd"
          borderRadius="10px"
          class_name="grid__item"
          onMouseEnter={e => {
                     setStyle({display: paragraph.ParagraphId});
                 }}
                 onMouseLeave={e => {
                     setStyle({display: 'none'})
                 }}
        >
        <div>
          <Button
              onClick={() => {navigator.clipboard.writeText(`${window.location.origin}/gkcite/cite/${paragraph.ParagraphId}`)}}
              color="error"
              size="small"
              variation="link"
              ariaLabel="Link paragraph"
              style={{
                position: 'absolute',
                top: '0px',
                right: '60px',
                height: '0px',
                width: '0px',
                display: paragraph.ParagraphId === style.display? 'flex' : 'none'
              }}
            >
              <Icon
                marginTop="2px"
                marginLeft="2px"
                pathData="M18 16.08c-.76 0-1.44.3-1.96.77L8.91 12.7c.05-.23.09-.46.09-.7s-.04-.47-.09-.7l7.05-4.11c.54.5 1.25.81 2.04.81 1.66 0 3-1.34 3-3s-1.34-3-3-3-3 1.34-3 3c0 .24.04.47.09.7L8.04 9.81C7.5 9.31 6.79 9 6 9c-1.66 0-3 1.34-3 3s1.34 3 3 3c.79 0 1.5-.31 2.04-.81l7.12 4.16c-.05.21-.08.43-.08.65 0 1.61 1.31 2.92 2.92 2.92 1.61 0 2.92-1.31 2.92-2.92s-1.31-2.92-2.92-2.92z"
                viewBox={{
                  width: 27,
                  height: 27,
                }}
                ariaLabel="Search"
              />    
          </Button>
          { !isReadOnly &&
              <div>   
                <Button
                  onClick={() => onDelete(paragraph.ParagraphId)}
                  color="error"
                  size="small"
                  variation="link"
                  ariaLabel="Delete paragraph"
                  style={{
                    position: 'absolute',
                    top: '0px',
                    right: '0px',
                    height: '0px',
                    width: '0px'
                  }}
                >
                  <Icon
                    pathData="M286.559,107.182c-5.839-5.877-15.336-5.908-21.214-0.069l-66.539,66.106l-66.572-66.572 c-5.857-5.858-15.355-5.858-21.213,0c-5.858,5.858-5.858,15.355,0,21.213l66.502,66.503l-65.968,65.539 c-5.877,5.839-5.907,15.336-0.069,21.213c2.933,2.952,6.786,4.428,10.642,4.428c3.822,0,7.646-1.452,10.572-4.359l66.037-65.608 l66.073,66.073c2.929,2.929,6.768,4.394,10.606,4.394c3.838,0,7.678-1.465,10.606-4.394c5.858-5.858,5.858-15.355,0-21.213 l-66.004-66.004l66.47-66.038C292.366,122.556,292.396,113.059,286.559,107.182z"
                    viewBox={{
                      width: 400,
                      height: 400,
                    }}
                    ariaLabel="Search"
                  />    
                </Button>
                <Button
                  id={paragraph.ParagraphId}
                  onClick={() => onEdit(paragraph)}
                  color="error"
                  size="small"
                  variation="link"
                  ariaLabel="Edit paragraph"
                  style={{
                    position: 'absolute',
                    top: '1px',
                    right: '30px',
                    height: '0px',
                    width: '0px',
                    display: paragraph.ParagraphId === style.display? 'flex' : 'none'
                  }}
                >
                  <Icon
                    pathData="M303.85,138.388c-8.284,0-15,6.716-15,15v127.347c0,21.034-17.113,38.147-38.147,38.147H68.904 c-21.035,0-38.147-17.113-38.147-38.147V100.413c0-21.034,17.113-38.147,38.147-38.147h131.587c8.284,0,15-6.716,15-15 s-6.716-15-15-15H68.904c-37.577,0-68.147,30.571-68.147,68.147v180.321c0,37.576,30.571,68.147,68.147,68.147h181.798 c37.576,0,68.147-30.571,68.147-68.147V153.388C318.85,145.104,312.134,138.388,303.85,138.388z M333.988,11.758l-0.42-0.383C325.538,4.04,315.129,0,304.258,0c-12.187,0-23.888,5.159-32.104,14.153L116.803,184.231 c-1.416,1.55-2.49,3.379-3.154,5.37l-18.267,54.762c-2.112,6.331-1.052,13.333,2.835,18.729c3.918,5.438,10.23,8.685,16.886,8.685 c0,0,0.001,0,0.001,0c2.879,0,5.693-0.592,8.362-1.76l52.89-23.138c1.923-0.841,3.648-2.076,5.063-3.626L336.771,73.176 C352.937,55.479,351.69,27.929,333.988,11.758z M130.381,234.247l10.719-32.134l0.904-0.99l20.316,18.556l-0.904,0.99 L130.381,234.247z M314.621,52.943L182.553,197.53l-20.316-18.556L294.305,34.386c2.583-2.828,6.118-4.386,9.954-4.386 c3.365,0,6.588,1.252,9.082,3.53l0.419,0.383C319.244,38.922,319.63,47.459,314.621,52.943z"
                    viewBox={{
                      width: 400,
                      height: 400,
                    }}
                    ariaLabel="Search"
                  />   
                </Button>
              </div>
            }
          </div>
          <div
            className="tooltip"
            style={{
              position: 'absolute',
              top: '15px',
              right: '0px',
              // opacity: 0.5,
              transition: 'opacity 0.3s ease',
              backgroundColor: '#fff',
              padding: '5px',
              borderRadius: '5px',
              boxShadow: '0 0 5px rgba(0, 0, 0, 0)',
              zIndex: '0',
              height: '20px',
              width: '20px',
              backgroundColor: 'transparent'
              // color
            }}
            title={`Paragraph ID: ${paragraph.ParagraphId}\nCreated At: ${paragraph.CreatedAt}\nLast Updated At: ${paragraph.UpdatedAt}`}
          />
           
          <View
            as="a"
            href={paragraph.SourceLink?paragraph.SourceLink:null}
            target="_blank"
            rel="noopener noreferrer"
            style={{ textDecoration: 'none', color: 'inherit' }} // Override default anchor styles
          >
          <Markdown style={{ margin: '0px' }}>{paragraph.Text}</Markdown>
          </View>
          <p
                style={{
                  width: '30%',
                  position: 'absolute',
                  bottom: '0',
                  left: '0',
                  fontSize: '0.6rem', // Small font size
                  margin: '0',
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap',
                  color: 'rgb(180, 180, 180)',
                  paddingLeft:'10px'
                }}
              >
                {paragraph.SourceLink}
            </p>
            <p
                style={{
                  width: '30%',
                  position: 'absolute',
                  bottom: '0',
                  right: '5px',
                  fontSize: '0.6rem', // Small font size
                  margin: '0',
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap',
                  color: 'rgb(180, 180, 180)',
                  paddingLeft:'10px',
                  textAlign : 'right'
                }}
              >
                {paragraph.Tags}
            </p>
          </Card>
        </div>
      ))}
      </div>
      );

};

export default ParagraphList;