// gkcite.js

import React, { useState, useEffect } from 'react';
import AddParagraphModal from '../components/AddParagraphModal';
import FilterParagraphs from '../components/FilterParagraphs';
import ParagraphList from '../components/ParagraphList';
import { View, Button, Flex, Heading, Divider, Tabs } from '@aws-amplify/ui-react';
import '@aws-amplify/ui-react/styles.css';


const GKCite = () => {
  const [paragraphs, setParagraphs] = useState([]);
  const [filterTags, setFilterTags] = useState('');
  const [isAddModalOpen, setAddModalOpen] = useState(false);
  const [editingParagraph, setEditingParagraph] = useState(null);
  const [categories, setCategories] = useState([]);
  const [tab, setTab] = useState(localStorage.getItem("CatTabVal") || 'Cites');
  const isReadOnly = localStorage.getItem("password") == null;

  const handleEdit = (paragraphToEdit) => {
    setEditingParagraph(paragraphToEdit); // Set the paragraph to be edited
    setAddModalOpen(true);
  };

  const openAddModal = () => {
    setEditingParagraph(null); // Reset editing paragraph
    setAddModalOpen(true);
  };


  const extractCategories = (paragraphItems) => {
    let categoryInfo = {};
    paragraphItems.forEach(element => {
        if (element.Category && element.CreatedAt) {
            if (!(element.Category in categoryInfo) || element.CreatedAt < categoryInfo[element.Category]) {
                categoryInfo[element.Category] = element.CreatedAt;
            }
        }
    });

    // Sort categories by earliest CreatedAt date
    let sortedCategories = Object.keys(categoryInfo).sort((a, b) => {
        return new Date(categoryInfo[a]) - new Date(categoryInfo[b]);
    });
    return sortedCategories;
  }

  const fetchParagraphs = async () => {
    try {
      const response = await fetch('https://l1az6ozvxa.execute-api.us-east-1.amazonaws.com/alpha/gkcite/api', {
        method: 'GET',
        headers: {
          "Content-Type": "application/json",
          "Authorization": `${btoa("123456")}:${btoa(localStorage.getItem("password"))}`
        }
      });

      if (!response.ok) {
        throw new Error(`Error fetching paragraphs. Status: ${response.status}`);
      }

      const paragraphsData = await response.json();
      // Assuming the response has a property named 'items' containing the array of paragraphs
      setParagraphs(paragraphsData.items);
      setCategories(extractCategories(paragraphsData.items))
      // console.log(categories);
    } catch (error) {
      console.error('Error fetching paragraphs:', error);
    }
  };

  const deleteParagraph = async (paragraphId) => {
    try {
      const response = await await fetch('https://l1az6ozvxa.execute-api.us-east-1.amazonaws.com/alpha/gkcite/api', {
        method: 'DELETE',
        headers: {
          "Content-Type": "application/json",
          "Authorization": `${btoa("123456")}:${btoa(localStorage.getItem("password"))}`
        },
        body: JSON.stringify({
          paragraph_id: paragraphId
        }),
    });

    if (!response.ok) {
      throw new Error(`Error deleting paragraph. Status: ${response.status}`);
    }

    // Assuming the response contains the newly added paragraph
    const newParagraph = await response.json();
    // Use the new paragraph data as needed
    console.log(' paragraph Deleted:', newParagraph);
  } catch (error) {
    console.error('Error deleting paragraph:', error);
  }
};


  useEffect(() => {
    fetchParagraphs();
  }, []);

  const handleAdd = (newParagraph) => {
    fetchParagraphs();
    setAddModalOpen(false);
  };

  const handleDelete = async (deletedParagraphId) => {
    try {
      // console.log(deletedParagraphId);
      // console.log(paragraphs.filter((p) => p.ParagraphId !== deletedParagraphId));
      
      deleteParagraph(deletedParagraphId);
      setParagraphs((prevParagraphs) =>
        prevParagraphs.filter((paragraph) => paragraph.ParagraphId !== deletedParagraphId)
      );
    } catch (error) {
      console.error('Error deleting paragraph:', error);
    }
  };

  const filterParagraphs = (allParas, filterContent, tabVal) => {
      // console.log(tabVal, filterContent)
      // if (!filterContent) {return allParas;}
      const filtered = allParas.filter(paragraph => {
      const lowercaseFilterContent = filterContent.toLowerCase();
      // console.log(paragraph.Tags);
      var condn = (
        //assuming tags are string
        (paragraph.Tags && paragraph.Tags.toLowerCase().includes(lowercaseFilterContent)) ||
        (paragraph.Text && paragraph.Text.toLowerCase().includes(lowercaseFilterContent)) ||
        (paragraph.SourceLink && paragraph.SourceLink.toLowerCase().includes(lowercaseFilterContent))
      );
      // console.log(condn);
      condn = condn && (tabVal==='All' || (paragraph.Category && paragraph.Category === tabVal) || (!paragraph.Category && tabVal==='Cites') )
      return condn;  
    });
    // console.log(filtered)

    return filtered;
  }

  const generateCategoryArray = (categories, paragraphs, filterTags, handleDelete, handleEdit) => {
    return categories.map(category => {
        return {
            label: category,
            value: category,
            content: (
                <ParagraphList 
                    paragraphs={filterParagraphs(paragraphs, filterTags, category)} 
                    onDelete={handleDelete} 
                    onEdit={handleEdit} 
                />
            )
        };
    });
}

  return (

       <View padding="1rem">
          <script src="https://cdnjs.cloudflare.com/ajax/libs/react-modal/3.14.3/react-modal.min.js"
             integrity="sha512-MY2jfK3DBnVzdS2V8MXo5lRtr0mNRroUI9hoLVv2/yL3vrJTam3VzASuKQ96fLEpyYIT4a8o7YgtUs5lPjiLVQ=="
             crossOrigin="anonymous"
             referrerPolicy="no-referrer"></script>
             <script src="https://unpkg.com/packery@2.1/dist/packery.pkgd.js"></script>


            <Heading level={1}>GKCite</Heading>
            <Divider marginBottom="1rem" />
                        
            <Flex direction="row" alignItems="left" justifyContent="space-between" marginBottom="1rem">
              <FilterParagraphs filterState={filterTags} setFilter={setFilterTags} />
              <Flex direction="column" alignItems="top">
                <Button  colorTheme="overlay" isDisabled={tab==='All' || isReadOnly} onClick={() => openAddModal()} maxWidth="100px" style={{ backgroundColor: 'rgb(4, 125, 149)', color: 'white' }} >Create</Button>
              </Flex>
            </Flex>

            <Tabs
                  className="navbar"
                  value={tab}
                  onValueChange={(tab) => {localStorage.setItem('CatTabVal', tab); setTab(tab);}}
                  defaultValue="1"
                  style={{ top: '50px' }}
                  items={[
                    { label: 'All', value: 'All', content: 
                        (<ParagraphList paragraphs={filterParagraphs(paragraphs, filterTags, 'All')} onDelete={handleDelete} onEdit={handleEdit} isReadOnly={isReadOnly}/>)
                    },
                    ...generateCategoryArray(categories, paragraphs, filterTags, handleDelete, handleEdit) 
                  ]}
                  isLazy
              />

            
              {isAddModalOpen && (
                <AddParagraphModal onClose={() => setAddModalOpen(false)} onAdd={handleAdd} visible={isAddModalOpen} editingParagraph={editingParagraph} category={tab}/>
              )}

           


          </View>
  );
};

export default GKCite;
